import styled from "styled-components";
import { Link , useNavigate } from "react-router-dom";
import useStore from "../Zusatand/Admin";
import { useState , useEffect } from "react";


function CommonTop() {

    const { Login , LoginCancel , LoginCertify } = useStore();
    const [search, setSearch] = useState(''); //  
    const navigation = useNavigate()

    //   
  useEffect(() => {
    if (localStorage.getItem('maintail')) {
        LoginCertify();
         // 
    }
  }, [Login]);


    return (
        <HeaderBox>
            {/*     */}
            <Logo>
                <img src = "https://firststepimage.s3.ap-northeast-2.amazonaws.com/Admin/Admin_Text.png" style={{ width:"100%" , height:"auto"}}/>
            </Logo>
            <InputBox
                name="search"
                placeholder=" ."
                onChange={(e) => setSearch(e.target.value)}
                value={search}
            />
            <Magnify 
            onClick={() => navigation('/Search')}
            src= "https://firststepimage.s3.ap-northeast-2.amazonaws.com/Main/Main_Search.png" alt="search_img"/>
        </HeaderBox>
    );
}

export default CommonTop;

