import { useState, useEffect } from "react";
import styles from "./css/Login.module.css";
import { useNavigate } from "react-router-dom";
import useStore from "../Zusatand/Admin";

function Login() {

    const { Login, LoginCertify } = useStore();
    const [email, setEmail] = useState(''); //  
    const [emailClick, setEmailClick] = useState(true); //  
    const [PassWord, setPassWord] = useState(''); //  
    const [PassWordClick, setPassWordClick] = useState(true); //  
    const [PassWord_fail, setPassWord_fail] = useState(true); //     
    const [check, setCheck] = useState(true);
    const navigate = useNavigate();

    //      
    useEffect(() => {
        if (Login === false) {
            navigate('/')
        }
    }, [Login, navigate]);

    // 
    function Login_input() {

        setEmailClick(true);
        setPassWordClick(true);

        fetch("/api/user/login", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                name: email,
                password: PassWord,
            }),
        })
            .then(res => res.json())
            .then(data => {
                if (data.result === 'success') {
                    alert(' .')
                    navigate('/')
                    LoginCertify();
                    localStorage.setItem('id', email) // 
                    localStorage.setItem('maintail', check) //   
                }
                else if (data.result === 'fail') {
                    alert('   .')
                    setPassWord_fail(false)
                }
            })

    }

    //  Focus
    function EmainFocus(){
        setEmailClick(false)
        setPassWordClick(true)
    }

    //  Focus
    function PasswordFocus(){
        setEmailClick(true)
        setPassWordClick(false)
    }

    //   Focus
    function PasswordFailFocus(){
        setEmailClick(true)
        setPassWordClick(false)
        setPassWord_fail(true)
    }

    return (
        <div className={styles.Menu}>

            {/* Header */}
            <div className={styles.Header} >
                <img src="https://kr.object.ncloudstorage.com/firststep/Main/Main/arrow-left.png" className={styles.Back_Arrow} alt="Total_img"
                    onClick={() => navigate('/')} />

            </div>

            {/* Intro */}
            <div className={styles.Intro}>
                <div className={styles.IntroText}>.<br /></div>
                <span className={styles.IntroSub}>    .</span>
            </div>

            {/*  */}
            <div className={styles.LoginBox}>


                {/*  */}
                {emailClick ?
                    <div className={styles.Email}>
                        <span className={styles.EmailText}></span>
                        <input
                            className={styles.EmailInput}
                            name="email"
                            placeholder=" ."
                            onChange={(e) => setEmail(e.target.value)}
                            // onClick={()=> EmainFocus()}
                            onFocus={() => EmainFocus(false)}
                            value={email}
                        />
                    </div>
                    :
                    <div className={styles.EmailClick}>
                        <span className={styles.EmailText}></span>
                        <input
                            className={styles.EmailInput}
                            name="email"
                            placeholder=" ."
                            onChange={(e) => setEmail(e.target.value)}
                            value={email}
                        />
                    </div>


                }

                {/*  */}

                {PassWord_fail && PassWordClick?
                    <div className={styles.Email}>
                        <span className={styles.EmailText}></span>
                        <input
                            className={styles.EmailInput}
                            name="email"
                            placeholder=", ,   8 "
                            onChange={(e) => setPassWord(e.target.value)}
                            value={PassWord}
                            type="password"
                            onFocus={()=> PasswordFocus()}
                        />
                    </div>
                    :PassWord_fail === false ?
                    <div className={styles.EmailFail}>
                        <span className={styles.EmailText}></span>
                        <input
                            className={styles.EmailInput}
                            name="email"
                            placeholder=", ,   8 "
                            onChange={(e) => setPassWord(e.target.value)}
                            onClick={()=> PasswordFailFocus()}
                            value={PassWord}
                            type="password"
                        />
                    </div>
                    :PassWordClick === false  ?
                    <div className={styles.EmailClick}>
                        <span className={styles.EmailText}></span>
                        <input
                            className={styles.EmailInput}
                            name="email"
                            placeholder=", ,   8 "
                            onChange={(e) => setPassWord(e.target.value)}
                            value={PassWord}
                            type="password"
                        />
                    </div>
                    :<></>
                }

                {/*  */}
                <div className={styles.AutoLogin}>
                    {check ?
                        <img src="https://firststepimage.s3.ap-northeast-2.amazonaws.com/Admin%2CLogin/Login_Check_No.png" alt="checkbox" className={styles.AutoLogincheck} onClick={() => setCheck(!check)} />
                        :
                        <img src="https://firststepimage.s3.ap-northeast-2.amazonaws.com/Admin%2CLogin/Login_Check.png" alt="checkbox" className={styles.AutoLogincheck} onClick={() => setCheck(!check)} />
                    }
                    
                </div>
            </div>



            {/*  */}
            <div className={styles.LoginBtn}>
                <div className={styles.Btn} onClick={() => Login_input()}>
                    <span></span>
                </div>
                <div className={styles.FindBox}>
                    <div onClick={() => navigate('/Find/id')}> </div>&nbsp;&nbsp;|&nbsp;&nbsp;
                    <div onClick={() => navigate('/Find/pw')}>   </div>
                </div>
            </div>


        </div>

    );
}

export default Login;

