import { useState, useEffect } from "react";
import styles from "../Common/css/Login.module.css";
import stylesSecond from "../Common/css/Admin.module.css";
import { useNavigate, useLocation } from "react-router-dom";
import styled from "styled-components";
import PayStore from "../Zusatand/Pay";


function ClassPay() {

    const navigate = useNavigate();
    const location = useLocation()
    const [data, setData] = useState([]);
    const [bank, setBank] = useState("");
    const { bank_info , bank_number_function } = PayStore();
    // const [nickname, setNickname] = useState([]);
    // console.log(location.pathname.split('/')[3]

    //  
    useEffect(() => {
        if(location.pathname.includes('end')){
            fetch(`/api/tutor/detail/${location.pathname.split('/')[4]}`, {
                method: 'GET',
            })
                .then(response => {
                    return response.json();
                })
                .then(data => {
                    setData(data[0])
                    // console.log(data)
                });
        }
        else{
            fetch(`/api/tutor/detail/${location.pathname.split('/')[3]}`, {
                method: 'GET',
            })
                .then(response => {
                    return response.json();
                })
                .then(data => {
                    setData(data[0])
                    // console.log(data)
                });
        }
       
    }, [data]);

    // 
    useEffect(() => {
        if (bank !== "") {
            bank_number_function(bank);
        }
    }, [bank]);



    return (
        <MentorText>
            <Top>
                <TopInner>
                    <img src="https://kr.object.ncloudstorage.com/firststep/Main/Main/arrow-left.png" style={{ width: "24px", height: "24px" }}
                        onClick={() => navigate(`/Consultng/detail/${data.id}`)} />
                    <span style={{ fontSize: "16px", fontWeight: "700", color: "#3F3F3F" }}></span>
                    <div style={{ width: "24px", height: "24px" }}></div>
                </TopInner>
            </Top>

           {/*  */}
            <PayBox>
                <span style={{ color:"#515151" , fontSize:"16px" , fontWeight:"600"}}></span>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span></span>
                    <span>{data.ProgramName}</span>
                </PayTitle>
                <PayTitle style={{ marginTop:"12px" , paddingBottom:"16px" , borderBottom:"1px solid #DCDCDC"}}>
                    <span></span>
                    <span>/{data.Time}</span>
                </PayTitle>
                <PayValue>
                    <span style={{ fontSize:"14px" , fontWeight:"700" }}>  </span>
                    <span  style={{ fontSize:"24px" , fontWeight:"700" }}>{data.Value}</span>
                </PayValue>
            </PayBox>
            
            <Devinder />

            {/*  */}
            <PayBox>
                <span style={{ color:"#515151" , fontSize:"16px" , fontWeight:"600"}}></span>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span></span>
                    <span></span>
                </PayTitle>
                <PayTitle style={{ marginTop:"12px" , paddingBottom:"16px" , paddinBottom:"24px"}}>
                    <span></span>
                    <span>/5080-4566-1010-14</span>
                </PayTitle>
            </PayBox>

            <Devinder />

             {/*    */}
             <PayBox style={{ paddingBottom:"32px"}}>
                <span style={{ color:"#515151" , fontSize:"16px" , fontWeight:"600"}}> </span>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span></span>
                    {location.pathname.includes('end') ?
                    <span style={{ color:"#00C563" }} onClick={()=> navigate(`/`)}>{bank_info}</span>
                    :
                    <span style={{ color:"#00C563" }} onClick={()=> navigate(`/Class/choice/bank/${data.id}`)}> </span>
                    }
                </PayTitle>
                <PayTitle style={{ marginTop:"12px" , paddingBottom:"16px" , paddinBottom:"24px" , flexDirection:"column"}}>
                    <span></span>
                    <input style={{ width: "100%", height: "56px" , marginTop:"16px" , border:"1px solid #DCDCDC" , borderRadius:"8px"}}
                                    onChange={(e) => setBank(e.target.value)}
                                    value={bank}
                                    placeholder=" "
                                    type="text"
                                />
                </PayTitle>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span></span>
                    <span style={{ color:"#00C563" }}>  </span>
                </PayTitle>
            </PayBox>

            <Devinder />

            <PayCertify>
                <div style={{ width:"24px" , height:"24px" , marginRight:"4px" , border:"1px solid #DCDCDC" }}/>
                <span style={{ fontSize:"16px" , fontWeight:"600" , color:"#515151"}}>   </span>
                <span style={{ fontSize:"16px" , fontWeight:"600" , color:"#00C563"}}>()</span>
            </PayCertify>


            {/*  */}
            <ProfileBtn onClick={() => navigate(`/Class/end/${data.id}`)}> 
                
            </ProfileBtn>
        </MentorText >
    );
}

export default ClassPay;

/*  */
const MentorText = styled.div`
width:540px;
height:auto;
display:flex;
justify-content: cneter;
align-items: center;
flex-direction:column;
padding-bottom:100px;
@media screen and (max-width: 540px) {
    width:100%;
}
`;

/*  */
const Top = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
width: 100%;
height: 60.48px;
@media screen and (max-width: 540px) {
    height:11.2vw;
	}
`;

/*   */
const TopInner = styled.div`
display: flex;
flex-direction:row;
justify-content:space-between;
align-items: center;
width: 90%;
height: 60.48px;
@media screen and (max-width: 540px) {
    height:11.2vw;
	}
`;

/*    */
const ProfileBtn = styled.div`
display: flex;
justify-content:center;
align-items: center;
width: 90%;
height: 50px;
background: #00C563;
border-radius: 8px;
color:white;
@media screen and (max-width: 540px) {
	}
`;

/*   1  */
const Devinder = styled.div`
width: 100%;
height: 14.4px;
background: #F1F2F3;
border-top: 1px solid #DCDCDC;
@media screen and (max-width: 540px) {
    height: 2.6vw;
}
`;

