const PayBox = styled.div`
display: flex;
justify-content:center;
align-items: flex-start;
flex-direction:column;
margin-top:42px;
width: 90%;
height: auto;
@media screen and (max-width: 540px) {
	}
`;

/*  ,  */
const PayTitle = styled.div`
display: flex;
justify-content:space-between;
align-items: flex-start;
flex-direction:row;
width: 90%;
height: auto;
font-weight: 400;
font-size: 14px;
color: #797979;
@media screen and (max-width: 540px) {
	}
`;

/*  */
const PayValue = styled.div`
display: flex;
justify-content:space-between;
align-items: center;
flex-direction:row;
width: 90%;
height: auto;
font-weight: 400;
font-size: 14px;
color: #515151;
margin-top:16px;
padding-bottom:32px;
@media screen and (max-width: 540px) {
	}
`;

/*  */
const PayCertify = styled.div`
display: flex;
justify-content:flex-start;
align-items: center;
flex-direction:row;
width: 90%;
margin-top:16px;
margin-bottom:63px;
height: auto;
@media screen and (max-width: 540px) {
	}
`;
