import { useState, useEffect } from "react";
import styles from "../Common/css/Login.module.css";
import stylesSecond from "../Common/css/Admin.module.css";
import { useNavigate, useLocation } from "react-router-dom";
import styled from "styled-components";
import PayStore from "../Zusatand/Pay";


function ApplyEnd() {

    const navigate = useNavigate();
    const location = useLocation()
    const [data, setData] = useState([]);
    const [bank, setBank] = useState("");
    const { bank_info , bank_number } = PayStore();
    // const [nickname, setNickname] = useState([]);
    // console.log(location.pathname.split('/')[3]

    //  
    useEffect(() => {
        
            fetch(`/api/mentor/detail/${location.pathname.split('/')[3]}`, {
                method: 'GET',
            })
                .then(response => {
                    return response.json();
                })
                .then(data => {
                    setData(data[0])
                    // console.log(data)
                });
        
       
    }, [data]);

    // 
    function Admin(){
        var timess = new Date();


        console.log( bank_info + bank_number + data.Value + "", localStorage.getItem('id') , data.User , "")
        fetch("/api/add/class/save/MentorProcess", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                mentor: data.User + "," + data.ProgramName,
                mentir: localStorage.getItem('id') ,
                pay: bank_info + "-" + bank_number + "-" + data.Value + "",
                time:timess , 
                category: "",
            
            }),
        })
            .then(res => res.json())
            .then(data => {
                if (data.result === "success") {
                    alert(".")
                    navigate('/')
                }
                else{
                    alert(" .")
                }
            })

    }


    return (
        <MentorText>
            <Top>
                <TopInner>
                <div style={{ width: "24px", height: "24px" }}></div>
                    <span style={{ fontSize: "16px", fontWeight: "700", color: "#3F3F3F" }}></span>
                    <div style={{ width: "24px", height: "24px" }}></div>
                </TopInner>
            </Top>

            <PayBox style={{ marginTop:"24px" , paddingBottom:"30px"}}>
                <span style={{ fontSize:"24px" , fontWeight:"400" , color:"#515151"}}>.</span>
                <div style={{ display:"flex" , flexDirection:"row"}}>
                    <span style={{ fontSize:"24px" , fontWeight:"600" , color:"#515151"}}>  </span>
                    <span style={{ fontSize:"24px" , fontWeight:"400" , color:"#515151"}}>.</span>
                </div>
                <span style={{ fontSize:"14px" , fontWeight:"400" , color:"#797979" , marginTop:"8px"}}>    .</span>
            </PayBox>

            <Devinder />

           {/*  */}
            <PayBox>
                <span style={{ color:"#515151" , fontSize:"16px" , fontWeight:"600"}}></span>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span></span>
                    <span>{data.ProgramName}</span>
                </PayTitle>
                <PayTitle style={{ marginTop:"12px" , paddingBottom:"16px" }}>
                    <span></span>
                    <span>/{data.Time}</span>
                </PayTitle>
              
            </PayBox>
            
   

            {/*  */}
            <PayBox>
                <span style={{ color:"#515151" , fontSize:"16px" , fontWeight:"600"}}></span>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span></span>
                    <span></span>
                </PayTitle>
                <PayTitle style={{ marginTop:"12px" , }}>
                    <span></span>
                    <span>/5080-4566-1010-14</span>
                </PayTitle>
                <PayTitle style={{ marginTop:"16px" , borderBottom:"1px solid #DCDCDC" , paddingBottom:"16px"}}>
                    <span></span>
                    <span>  </span>
                </PayTitle>
                <PayTitle style={{ marginTop:"16px"}}>
                    <span style={{ fontSize:"14px" , fontWeight:"700" , color:"#515151"}}>  </span>
                    <span  style={{ fontSize:"24px" , fontWeight:"700" , color:"#515151"}}>{data.Value}</span>
                </PayTitle>
            </PayBox>

  

           
    


            {/*  */}
            <ProfileBtn onClick={() => Admin()}>
                
            </ProfileBtn>
        </MentorText >
    );
}

export default ApplyEnd;

